/* left div */
.left-landing-block {
  width: 100%;
  height: 100vh;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
}

.tag-line-box {
  background-color: #e6a57d;
  border-radius: 30px;
  width: 75%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.MuiTypography-root.tag-line-1.MuiTypography-body1 {
  font-size: 35px;
  height: 55px;
  font-family: "Futura-Heavy", sans-serif;
  font-weight: bold;
  padding: 10px 10px 0 10px;
  text-align: center;
}
.MuiTypography-root.tag-line-2.MuiTypography-body1 {
  font-size: 35px;
  height: 45px;
  font-family: "Futura-Light", sans-serif;
  text-align: center;
  padding: 0;
}
.MuiTypography-root.tag-line-3.MuiTypography-body1 {
  font-size: 35px;

  font-family: "Futura-Heavy", sans-serif;
  font-weight: bold;
  padding: 0 10px 10px 10px;
  text-align: center;
}

/* Right div */
.right-landing-block {
  background-color: #d1efff;
  height: 100vh;
  position: relative;
}

.landing-page-title {
  font-size: 40px;
  font-weight: bold;
  padding: 5%;
  text-align: center;
}

.MuiTypography-root.landing-page-title-text.MuiTypography-h2 {
  font-family: "IntroRustBook-base", sans-serif;
  color: #0f3d7d;
}
.MuiTypography-root.landing-page-title-text-2.MuiTypography-h2 {
  font-family: "IntroRustBook-base", sans-serif;
  color: #e6a57d;
}

.MuiTypography-root.landing-page-sentence-text.MuiTypography-body1 {
  font-size: 26px;
  font-family: "Futura-Light", sans-serif;
  text-align: center;
  margin: 15px;
  padding: 0 50px;
}

/* Login button */

.landing-page-buttons {
  text-align: center;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.MuiButtonBase-root.MuiButton-root.MuiButton-outlined.landing-page-button {
  width: 200px;
  height: 50px;
  margin-left: 25%;
  margin-right: 25%;
  margin-bottom: 20px;
  padding: 15px 0;
  font-size: 15px;
  letter-spacing: 1px;
  border-radius: 30px;
}

/* Footer block */
.main-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footer-middle {
  background: #ebecf0;
  padding-top: 1rem;
  color: black;
  opacity: 0.4;
}
.footer-left-block {
  border-right: 1px solid white;
  padding-right: 50px;
  margin-right: 25px;
}

.footer-bottom {
  padding-top: 0.5rem;
}

.footer-right-block ul li {
  color: rgb(109, 109, 109);
}

.footer-right-block ul li:hover {
  color: rgb(172, 172, 172);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
